.pokeball {
    position: relative;
    width: 200px;
    height: 200px;
    background: #fff;
    border: 10px solid #000;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: inset -10px 10px 0 10px #ccc;
    -webkit-animation: spinning 1s infinite;
            animation: spinning 1s infinite;
}
.pokeball::before,
.pokeball::after {
    content:"";
    position: absolute;
}
.pokeball::before {
    background: red;
    width: 100%;
    height: 50%;
}
.pokeball::after {
    top: calc(50% - 10px);
    width: 100%;
    height: 20px;
    background: #000;
}
.pokeball__button {
    position: absolute;
    top: calc(50% - 25px);
    left: calc(50% - 25px);
    width: 30px;
    height: 30px;
    background: #7f8c8d;
    border: 10px solid #fff;
    border-radius: 50%;
    z-index: 10;
    box-shadow: 0 0 0 10px black;
}

@-webkit-keyframes spinning {
    from {
        transform: translate(0, 0) rotate(0);
    }
    to {
        transform: translate(0, 0) rotate(360deg);
    }
}

@keyframes spinning {
    from {
        transform: translate(0, 0) rotate(0);
    }
    to {
        transform: translate(0, 0) rotate(360deg);
    }
}

